<template>
  <div class="image-uploader">
    <el-form :model="form" label-width="100px">
      <el-form-item label="图片分类">
        <div class="category-select">
          <el-select v-model="form.categoryId" placeholder="选择图片分类" style="width: 200px">
            <el-option
              v-for="category in categories"
              :key="category.id"
              :label="category.name"
              :value="category.id"
            ></el-option>
          </el-select>
          <el-button type="primary" size="small" @click="showCategoryDialog = true">
            <el-icon><PlusIcon /></el-icon>
            添加分类
          </el-button>
          <el-button 
            type="danger" 
            size="small"
            @click="confirmDeleteCategory"
            :disabled="!form.categoryId || !canDeleteCategory(form.categoryId)"
          >
            <el-icon><DeleteIcon /></el-icon>
            删除分类
          </el-button>
        </div>
      </el-form-item>
      
      <el-form-item label="上传方式">
        <el-radio-group v-model="uploadType">
          <el-radio-button :value="'file'">
            <el-icon><Upload /></el-icon> 上传文件
          </el-radio-button>
          <el-radio-button :value="'url'">
            <el-icon><Link /></el-icon> 网络图片
          </el-radio-button>
        </el-radio-group>
      </el-form-item>
      
      <!-- 本地文件上传 -->
      <el-form-item v-if="uploadType === 'file'" label="本地图片">
        <div class="upload-container">
          <el-upload
            ref="uploadRef"
            :action="uploadUrl"
            :headers="uploadHeaders"
            :data="uploadData"
            name="image"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :on-success="handleSuccess"
            :on-error="handleError"
            :before-upload="beforeUpload"
            :auto-upload="true"
            multiple
            :limit="10"
            list-type="picture-card"
            :on-exceed="handleExceed"
            drag
          >
            <div class="upload-drag-area">
              <el-icon class="upload-icon"><PlusIcon /></el-icon>
              <div class="upload-text">点击或拖拽图片到此处上传</div>
              <div class="el-upload__tip">
                支持JPG/PNG/GIF格式，单个文件不超过5MB，最多10张
              </div>
            </div>
          </el-upload>
          <div class="upload-controls" v-if="fileList.length > 0">
            <el-button type="primary" @click="submitUpload">
              <el-icon><Upload /></el-icon>
              批量上传
            </el-button>
            <el-button @click="clearUpload">
              <el-icon><DeleteIcon /></el-icon>
              清空
            </el-button>
          </div>
        </div>
      </el-form-item>
      
      <!-- URL上传 -->
      <template v-if="uploadType === 'url'">
        <el-form-item label="图片URL">
          <div class="url-input">
            <el-input
              v-model="form.imageUrl"
              placeholder="请输入图片URL地址"
              clearable
              style="width: 400px"
            >
              <template #prefix>
                <el-icon><Link /></el-icon>
              </template>
            </el-input>
            <el-button type="primary" @click="uploadByUrl" :loading="uploading">
              <el-icon><Upload /></el-icon>
              上传
            </el-button>
          </div>
          <div class="url-preview" v-if="form.imageUrl">
            <div class="url-preview-label">URL预览:</div>
            <div class="url-preview-image">
              <img :src="form.imageUrl" alt="URL预览">
            </div>
          </div>
          <div class="el-upload__tip">
            支持JPG/PNG/GIF格式，支持批量上传（每行一个URL）
          </div>
        </el-form-item>
        
        <el-form-item label="批量URL">
          <el-input
            v-model="form.batchUrls"
            type="textarea"
            :rows="5"
            placeholder="每行输入一个图片URL"
          ></el-input>
          <div class="upload-controls" style="margin-top: 10px;">
            <el-button type="primary" @click="uploadBatchUrls" :loading="uploading">
              <el-icon><Upload /></el-icon>
              批量上传
            </el-button>
            <el-button @click="form.batchUrls = ''">
              <el-icon><DeleteIcon /></el-icon>
              清空
            </el-button>
          </div>
        </el-form-item>
      </template>
    </el-form>
    
    <!-- 图片预览 -->
    <el-dialog v-model="previewVisible" title="图片预览" width="600px">
      <div class="preview-dialog-content">
        <img :src="previewUrl" alt="Preview">
      </div>
    </el-dialog>
    
    <!-- 添加分类对话框 -->
    <el-dialog v-model="showCategoryDialog" title="添加图片分类" width="500px">
      <el-form :model="categoryForm" label-width="100px" ref="categoryFormRef">
        <el-form-item label="分类名称" prop="name" :rules="[{ required: true, message: '请输入分类名称', trigger: 'blur' }]">
          <el-input v-model="categoryForm.name" placeholder="请输入分类名称"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="showCategoryDialog = false">取消</el-button>
          <el-button type="primary" @click="addCategory" :loading="addingCategory">
            确认
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { ref, reactive, computed, onMounted } from 'vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import { Plus as PlusIcon, Upload, Link, Delete as DeleteIcon } from '@element-plus/icons-vue';
import { imageCategoryAPI } from '../utils/api';
import axios from 'axios';

export default {
  name: 'ImageUploader',
  components: {
    PlusIcon,
    Upload,
    Link,
    DeleteIcon
  },
  emits: ['upload-success'],
  setup(props, { emit }) {
    const uploadRef = ref(null);
    const categoryFormRef = ref(null);
    const categories = ref([]);
    const previewVisible = ref(false);
    const previewUrl = ref('');
    const showCategoryDialog = ref(false);
    const addingCategory = ref(false);
    const uploadType = ref('file'); // 默认为本地上传
    const fileList = ref([]);
    const uploading = ref(false);
    
    const form = reactive({
      categoryId: '',
      imageUrl: '',
      batchUrls: ''
    });
    
    const categoryForm = reactive({
      name: ''
    });
    
    // 上传地址
    const uploadUrl = computed(() => {
      return 'http://8.148.21.21:3000/api/images/upload';
    });
    
    // 上传头部
    const uploadHeaders = computed(() => {
      return {
        'Authorization': `Bearer ${localStorage.getItem('token')}`
      };
    });
    
    // 上传参数
    const uploadData = computed(() => {
      return {
        category_id: form.categoryId
      };
    });
    
    // 获取图片分类
    const fetchCategories = async () => {
      try {
        const response = await imageCategoryAPI.getCategories();
        categories.value = response.data.data;
        
        // 默认选中第一个分类
        if (categories.value.length > 0 && !form.categoryId) {
          form.categoryId = categories.value[0].id;
        }
      } catch (error) {
        console.error('获取图片分类失败:', error);
        ElMessage.error('获取图片分类失败');
      }
    };
    
    // 添加图片分类
    const addCategory = async () => {
      if (!categoryForm.name.trim()) {
        ElMessage.warning('请输入分类名称');
        return;
      }
      
      try {
        addingCategory.value = true;
        await imageCategoryAPI.addCategory({
          name: categoryForm.name.trim()
        });
        
        ElMessage.success('分类添加成功');
        showCategoryDialog.value = false;
        categoryForm.name = '';
        
        // 重新获取分类列表
        await fetchCategories();
      } catch (error) {
        console.error('添加分类失败:', error);
        ElMessage.error(error.response?.data?.message || '添加分类失败');
      } finally {
        addingCategory.value = false;
      }
    };
    
    // 处理上传超过限制
    const handleExceed = () => {
      ElMessage.warning('一次最多上传10张图片');
    };
    
    // 上传前检查
    const beforeUpload = (file) => {
      if (!form.categoryId) {
        ElMessage.warning('请先选择图片分类');
        return false;
      }
      
      const isImage = /^image\/(jpeg|png|gif|jpg)$/.test(file.type);
      if (!isImage) {
        ElMessage.error('只能上传JPG/PNG/GIF格式的图片');
        return false;
      }
      
      const isLt5M = file.size / 1024 / 1024 < 5;
      if (!isLt5M) {
        ElMessage.error('图片大小不能超过5MB');
        return false;
      }
      
      fileList.value.push(file);
      return true;
    };
    
    // 提交上传
    const submitUpload = () => {
      if (uploadRef.value) {
        uploadRef.value.submit();
      }
    };
    
    // 通过URL上传图片
    const uploadByUrl = async () => {
      if (!form.categoryId) {
        ElMessage.warning('请先选择图片分类');
        return;
      }
      
      if (!form.imageUrl) {
        ElMessage.warning('请输入图片URL');
        return;
      }
      
      try {
        uploading.value = true;
        
        const response = await axios.post(
          'http://8.148.21.21:3000/api/images/upload-by-url',
          {
            url: form.imageUrl,
            category_id: form.categoryId
          },
          {
            headers: uploadHeaders.value
          }
        );
        
        if (response.data.error) {
          ElMessage.error(response.data.message || 'URL上传失败');
          return;
        }
        
        ElMessage.success('图片URL上传成功');
        form.imageUrl = '';
        emit('upload-success', response.data.data);
      } catch (error) {
        console.error('URL上传失败:', error);
        ElMessage.error(error.response?.data?.message || 'URL上传失败');
      } finally {
        uploading.value = false;
      }
    };
    
    // 批量上传URL
    const uploadBatchUrls = async () => {
      if (!form.categoryId) {
        ElMessage.warning('请先选择图片分类');
        return;
      }
      
      if (!form.batchUrls) {
        ElMessage.warning('请输入图片URL');
        return;
      }
      
      // 分割每行URL
      const urls = form.batchUrls.split('\n').filter(url => url.trim());
      
      if (urls.length === 0) {
        ElMessage.warning('没有有效的URL地址');
        return;
      }
      
      try {
        uploading.value = true;
        
        const response = await axios.post(
          'http://8.148.21.21:3000/api/images/upload-batch-urls',
          {
            urls,
            category_id: form.categoryId
          },
          {
            headers: uploadHeaders.value
          }
        );
        
        if (response.data.error) {
          ElMessage.error(response.data.message || '批量URL上传失败');
          return;
        }
        
        ElMessage.success(`成功上传${response.data.data.length}张图片`);
        form.batchUrls = '';
        emit('upload-success', response.data.data);
      } catch (error) {
        console.error('批量URL上传失败:', error);
        ElMessage.error(error.response?.data?.message || '批量URL上传失败');
      } finally {
        uploading.value = false;
      }
    };
    
    // 预览图片
    const handlePreview = (file) => {
      previewUrl.value = file.url;
      previewVisible.value = true;
    };
    
    // 移除图片
    const handleRemove = (file) => {
      // 从fileList中移除
      const index = fileList.value.indexOf(file);
      if (index !== -1) {
        fileList.value.splice(index, 1);
      }
    };
    
    // 上传成功
    const handleSuccess = (response) => {
      if (response.error) {
        ElMessage.error(response.message || '上传失败');
        return;
      }
      
      ElMessage.success('图片上传成功');
      emit('upload-success', response.data);
    };
    
    // 上传失败
    const handleError = (err) => {
      console.error('上传图片失败:', err);
      ElMessage.error('上传图片失败');
    };
    
    // 清空上传
    const clearUpload = () => {
      if (uploadRef.value) {
        uploadRef.value.clearFiles();
      }
      fileList.value = [];
    };

    // 确认删除分类
    const confirmDeleteCategory = () => {
      if (!form.categoryId) {
        ElMessage.warning('请先选择分类');
        return;
      }
      
      const category = categories.value.find(c => c.id === form.categoryId);
      if (!category) {
        ElMessage.warning('未找到该分类');
        return;
      }
      
      // 检查分类中是否有图片
      if (category.image_count > 0) {
        ElMessage.error(`无法删除分类"${category.name}"，该分类下还有 ${category.image_count} 张图片`);
        return;
      }
      
      ElMessageBox.confirm(
        `确定要删除分类"${category.name}"吗？删除后无法恢复。`,
        '删除分类',
        {
          confirmButtonText: '确定删除',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).then(() => {
        deleteCategory(form.categoryId);
      }).catch(() => {
        // 用户取消操作
      });
    };
    
    // 删除分类
    const deleteCategory = async (categoryId) => {
      try {
        await imageCategoryAPI.deleteCategory(categoryId);
        ElMessage.success('分类删除成功');
        
        // 清空当前选择的分类
        form.categoryId = '';
        
        // 重新获取分类列表
        await fetchCategories();
      } catch (error) {
        console.error('删除分类失败:', error);
        const errorMsg = error.response?.data?.message || '删除分类失败';
        ElMessage.error(errorMsg);
      }
    };
    
    // 检查分类是否可以删除
    const canDeleteCategory = (categoryId) => {
      const category = categories.value.find(c => c.id === categoryId);
      return category && category.image_count === 0;
    };
    
    onMounted(() => {
      fetchCategories();
    });
    
    return {
      uploadRef,
      categoryFormRef,
      categories,
      form,
      categoryForm,
      previewVisible,
      previewUrl,
      showCategoryDialog,
      addingCategory,
      uploadUrl,
      uploadHeaders,
      uploadData,
      uploadType,
      fileList,
      uploading,
      fetchCategories,
      addCategory,
      handleExceed,
      beforeUpload,
      handlePreview,
      handleRemove,
      handleSuccess,
      handleError,
      clearUpload,
      submitUpload,
      uploadByUrl,
      uploadBatchUrls,
      confirmDeleteCategory,
      canDeleteCategory
    };
  }
};
</script>

<style scoped>
.image-uploader {
  margin-bottom: 20px;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  /* 防止ResizeObserver循环错误 */
  overflow: hidden !important;
}

.category-select {
  display: flex;
  align-items: center;
  gap: 10px;
}

.upload-container {
  width: 100%;
  border: 1px dashed #dcdfe6;
  border-radius: 8px;
  padding: 20px;
  background-color: #f8f9fa;
  text-align: center;
}

.upload-drag-area {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #606266;
}

.upload-icon {
  font-size: 48px;
  color: #c0c4cc;
  margin-bottom: 10px;
}

.upload-text {
  font-size: 16px;
  margin-bottom: 10px;
}

.el-upload__tip {
  font-size: 12px;
  color: #909399;
  margin-top: 10px;
}

.upload-controls {
  margin-top: 15px;
  display: flex;
  gap: 10px;
  justify-content: center;
}

.url-input {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

.url-preview {
  margin: 16px 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.url-preview-label {
  font-weight: bold;
  color: #606266;
}

.url-preview-image {
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  padding: 8px;
  background-color: #fff;
  display: inline-block;
}

.url-preview-image img {
  max-width: 200px;
  max-height: 200px;
  object-fit: contain;
}

.dialog-footer {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}

.preview-dialog-content {
  display: flex;
  justify-content: center;
  background-color: #000;
  padding: 20px;
}

.preview-dialog-content img {
  max-width: 100%;
  max-height: 400px;
  object-fit: contain;
}
</style> 