import axios from './axios';

// Auth API
export const authAPI = {
  register: (userData) => axios.post('/api/auth/register', userData),
  login: (credentials) => axios.post('/api/auth/login', credentials),
  getMe: () => axios.get('/api/auth/me')
};

// Shop API
export const shopAPI = {
  getShops: () => axios.get('/api/shops'),
  addShop: (shopData) => axios.post('/api/shops', shopData),
  getShop: (id) => axios.get(`/api/shops/${id}`),
  updateShop: (id, shopData) => axios.put(`/api/shops/${id}`, shopData),
  deleteShop: (id) => axios.delete(`/api/shops/${id}`),
  queryShopInfo: (id) => axios.post(`/api/shops/${id}/query`),
  getShopAuthorize: (id) => axios.get(`/api/shops/${id}/authorize`)
};

// Image Categories API
export const imageCategoryAPI = {
  getCategories: () => axios.get('/api/image-categories'),
  addCategory: (categoryData) => axios.post('/api/image-categories', categoryData),
  deleteCategory: (id, options = {}) => axios.delete(`/api/image-categories/${id}`, { params: options })
};

// Images API
export const imageAPI = {
  getImages: (params) => axios.get('/api/images', { params }),
  uploadImage: (formData) => axios.post('/api/images/upload', formData, {
    headers: { 'Content-Type': 'multipart/form-data' }
  }),
  compressImage: (imageId) => axios.post('/api/images/compress', { imageId })
};

// Description API
export const descriptionAPI = {
  generateDescription: (keywords, config = {}) => axios.post('/api/descriptions/generate', { keywords }, config),
  generateDescriptionAsync: (keywords, config = {}) => axios.post('/api/descriptions/generate', { keywords, useAsyncMode: true }, config),
  checkAsyncResult: (workflowId, executeId, config = {}) => axios.get(`/api/descriptions/async-result/${workflowId}/${executeId}`, config)
};

// Products API
export const productAPI = {
  createProduct: (productData) => axios.post('/api/products', productData),
  publishProduct: (id, publishData) => axios.post(`/api/products/${id}/publish`, publishData),
  deleteProduct: (id) => axios.delete(`/api/products/${id}`),
  testPublishProduct: (id, testData) => axios.post(`/api/products/${id}/test-publish`, testData)
};

// Dashboard API
export const dashboardAPI = {
  getStats: () => axios.get('/api/dashboard')
};

// Coze API Test
export const cozeAPI = {
  testConnection: (testData) => axios.post('/api/test-coze-api', testData)
}; 